<template>
	<div class="create_wrap">
		<div class="add_role">
			<i  class="el-icon-plus" @click="addRoleFun"></i>
			<p>新建角色</p>
		</div>
		<!--创建角色弹出框-->
	    <el-dialog
		    title="新建角色"
		    :visible.sync="createDialogVisible"
		    width="40%"
		    :before-close="handleClose">
		 	<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
		 		<el-form-item label="角色名称:" prop="ROLE_NAME">
			    	<el-input v-model="ruleForm.ROLE_NAME" placeholder="角色名称" style="width:90%;"></el-input>
			    </el-form-item>
			    <el-form-item label="功能权限:">
			    	<el-tree
						:data="cascaderData"
						show-checkbox
						node-key="MENU_ID"
						:default-expanded-keys="rangeCheckedData"
					 	:default-checked-keys="rangeCheckedData"
						style="height: 150px;overflow:auto;"
						:props="defaultProps"
						ref="tree2">
					</el-tree>
				</el-form-item>
			</el-form>
		    <span slot="footer" class="dialog-footer">
			    <el-button type="primary" @click="addTreeFun('ruleForm')">保存</el-button>
			   
		  	</span>
		</el-dialog>
	</div>
</template>

<script>
	import {
		leftMenuPort
	} from 'api/model/leftMenu';   //权限树
	import {
		saveRolePort,	 //新增角色
	}from 'api/authority'
	import {
	  	getUserInfo,
	  	getUsers
	} from 'utils/auth';
  	export default {
	 	data(){
	 		return {
 			    createDialogVisible: false,  //创建角色弹框
 			    cascaderData:[], //权限树
 			    rangeCheckedData:[],//选中tree
	 			//tree字段命名
				defaultProps: {
		          children: 'subMenu',
		          label: 'MENU_NAME'
		        },
				ruleForm:{
					ROLE_NAME:''
				},
				rules: {
				 	ROLE_NAME: [
			            { required: true, message: '请输入角色名称', trigger: 'blur' },
			            { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
			        ],
				}
	 		}
	 	},
	 	created(){
	 		this.createDialogVisible = true
	 		this.addRoleTree()
	 	},
	 	methods:{
	 		//添加角色
	 		addRoleFun(){
	 			this.createDialogVisible = true
	 			this.addRoleTree()
	 		},
	 		//添加权限树
	    	addRoleTree(){
	    		this.createDialogVisible = true
	    		let req = {
		    		USER_ID:getUsers(),  // 
		    		RIGHTS:getUserInfo().RIGHTS,
	    	    }
   			
		    	leftMenuPort(req).then( data => {
		    	 	this.cascaderData = data
	//		  		console.log('菜单::'+JSON.stringify(data))
			  		
			    })
	    	},
	    	 	//添加角色确定
	    	addTreeFun(formName){
	    		this.$refs[formName].validate((valid) => {
					if (valid) {
	    				this.createDialogVisible = false
	    				this.rangeCheckedData = this.$refs.tree2.getCheckedKeys()
	    				console.log('this.rangeCheckedData:::'+JSON.stringify(this.rangeCheckedData))
	    				let resData = {
	    					ROLE_NAME:this.ruleForm.ROLE_NAME,
	    					PARENT_ID:'',
	    					RIGHTS:this.rangeCheckedData.map(item =>item).join(),   //权限
	    					ADD_QX:'',   //增加权限
	    					DEL_QX:'',   //删除
	    					EDIT_QX:'', //
	    					QUR_QX:'',
	    					ROLE_TYPE:'2'  //
	    				}
	    				saveRolePort(resData).then( res =>{
	    					console.log('保存'+JSON.stringify(res))
	    					this.$router.push({
	    						name:'binder_list'
	    					})
	    				})
	    				
	    				
					} else {
						console.log('error submit!!');
						return false;
					}
				});
	    	
	    	},
	    	handleClose(done) {
		        this.$confirm('确认关闭？')
		          .then(() => {
		            done();
		          })
		          .catch(() => {});
	      	}
	 	}
  	}
</script>

<style rel="stylesheet/scss" lang="scss" slot-scope="scope">
	.create_wrap{
		background: #FFFFFF;
		padding: 20px 0;
		.add_role{
			width: 150px;
			background:#EEEEEE;
			border-radius: 8px;
			padding: 25px 0;
			text-align:center;
			margin-left:20px;
			i{
				font-size: 50px;
				cursor: pointer;
			}
		}
	}
</style>



